.cab-page {
	&__inner {
		display: flex;
		@include m {
			display: block; } }
	&__nav {
		// float: left
		flex-basis: 200px;
		flex-grow: 0;
		padding-top: 11px;
		flex-shrink: 0;
		@include lg {
			// width: 180px
			flex-basis: 180px; }
		@include m {
			padding-top: 0;
			// float: none
			// width: 100%
			text-align: center;
			margin-bottom: 10px; }
		a {
			display: block;
			@include trans;
			margin-bottom: 25px;
			position: relative;
			padding-right: 10px;
			&:after {
				content: '';
				position: absolute;
				right: 0;
				top: 50%;
				transform: translateY(-50%);
				@include arr(10,22,$blue,l);
				display: none; }
			&:hover {
				color: $red;
				span {
					border-color: rgba($red, 0.4); } }
			&.is-active {
				@include m {
					color: $red;
					span {
						border-color: rgba($red, 0.4); } }
				&:after {
					display: block;
					@include m {
						display: none; } } }
			@include m {
				display: inline-block;
				vertical-align: top;
				margin: 10px; }
			span {
				border-bottom: 1px dashed rgba($blue, 0.4);
				@include trans; } } }
	&__content {
		flex-grow: 1;
		// float: right

		// width: calc(100% - 200px)
		@include lg {
 }			// width: calc(100% - 180px)
		@include m {
			// float: none
 } } }			// width: 100%
.cab-content {
	background: $white;
	border: 10px solid $blue;
	padding: 20px 30px;
	height: 100%;
	@include m {
		border: 4px solid $blue;
		padding: 8px 12px 14px; }
	&__top-info-line {
		display: flex;
		justify-content: space-between;
		margin-bottom: 54px;
		flex-wrap: wrap;
		@include lg {
			font-size: 14px; }
		@include m {
			margin-bottom: 15px;
			display: block;
			text-align: center; }
		strong {
			@include rob; }
		span {
			padding-right: 5px;
			&:last-of-type {
				padding-right: 0; }
			@include m {
				display: inline-block;
				vertical-align: top;
				margin: 10px;
				padding-right: 0; } } }
	&__title-block {
		font-size: 24px;
		color: $white;
		@include rob;
		background: $blue2;
		padding: 7px 15px;
		line-height: 1.1;
		margin-bottom: 10px;
		letter-spacing: 2.5px;
		display: inline-block;
		vertical-align: top;
		width: 100%;
		text-transform: uppercase;
		font-weight: 700;
		@include m {
			font-size: 16px;
			text-align: left;
			display: block; }
		&--black {
			color: $black; }
		&--bg-l {
			background: $blue1; }
		&--bg-l2 {
			background: $bg-blue; }
		&--mb-big {
			margin-bottom: 40px;
			@include m {
				margin-bottom: 25px; } }
		&--mb-m {
			margin-bottom: 30px;
			@include m {
				margin-bottom: 20px; } }
		&--min-half-width {
			width: auto;
			min-width: 50%;
			@include m {
				min-width: auto;
				width: 100%; } } }
	&__text-big {
		font-size: 32px;
		line-height: 1.1;
		font-weight: 700;
		@include rob;
		&--mb {
			margin-bottom: 30px; }
		@include m {
			font-size: 20px; } }
	&__block {
		&--mb {
			margin-bottom: 32px;
			@include m {
				margin-bottom: 20px; } } }
	&__justify {
		display: flex;
		justify-content: space-between;
		max-width: 1102px;
		flex-wrap: wrap;
		@include lg {
			font-size: 14px; }
		@include m {
			display: block;
			text-align: center; }
		&>span {
			@include m {
				display: inline-block;
				vertical-align: top;
				margin: 10px; } }
		&-item {
			text-align: left;
			padding-right: 5px;
			flex-basis: 20%;
			flex-grow: 0;
			word-wrap: break-word;

			@include m {
				max-width: 100%;
				padding-right: 0; }
			p {
				// word-wrap: break-word
				// white-space: pre-line
				word-break: break-all;
				@include m {
					display: inline-block;
					margin-bottom: 10px;
					vertical-align: top; }
				&.mb {
					@include m {
						margin-right: 10px; } } }
			&--long {
				flex-basis: 40%;
 } } }				// max-width: 40%
	&__centered-text-bold {
		@include rob;
		font-weight: 700;
		margin-bottom: 22px;
		text-align: center; }
	&__centered {
		text-align: center;
		margin-bottom: 34px;
		@include m {
			margin-bottom: 25px; } } }
.cabtwo-col {
	@include clr;
	margin-bottom: 55px;
	@include m {
		margin-bottom: 20px; }
	&__left, &__right {
		@include xs {
			float: none;
			width: 100%;
			padding: 0;
			text-align: center; } }
	&__left {
		padding-right: 15px;
		float: left;
		width: 50%;
		@include xs {
			margin-bottom: 20px; } }
	&__right {
		float: right;
		width: 50%;
		padding-left: 15px; } }
.cabtwo-col-btn {
	@include clr;
	&__left {
		float: left;
		width: calc(100% - 160px);
		@include m {
			float: none;
			width: 100%;
			margin-bottom: 15px; } }
	&__right {
		float: right;
		text-align: right;
		width: 160px;
		@include m {
			float: none;
			width: 100%;
			text-align: center; } } }
.cab-plans {
	display: flex;
	justify-content: space-between;
	margin-bottom: 32px;
	flex-wrap: wrap;
	@include xs {
		display: block;
		margin-bottom: 25px; }
	.plan-block {
		margin-right: 0;
		flex-basis: 230px;
		@include xs {
			margin-left: auto;
			margin-right: auto; }
		&__bottom-text {
			margin-bottom: 18px; }
		&--l {
			border: 1px solid $blue1; }
		&--m {
			border: 1px solid $blue2; }
		&--d {
			border: 1px solid $blue; } } }
.depos-list {}
.depos-item {
	margin-bottom: 43px;
	@include m {
		margin-bottom: 25px; }

	&--mb-non {
		margin-bottom: 0; }
	&__topline {
		margin-bottom: 20px;
		strong {
			display: inline-block;
			vertical-align: top;
			font-size: 24px;
			margin-right: 19px;
			@include m {
				font-size: 16px; } }
		span {
			font-size: 15px;
			display: inline-block;
			vertical-align: bottom;
			padding-bottom: 5px;
			@include m {
				font-size: 14px; } } } }
.justify-spec {
	display: flex;
	justify-content: space-between;
	&--mb {
		margin-bottom: 38px;
		@include m {
			margin-bottom: 20px; } }
	&--mb-big {
		margin-bottom: 55px;
		@include m {
			margin-bottom: 25px; } }
	@include xs {
		display: block; }
	&__part {
		flex-basis: 33.33333%;
		text-align: center;
		@include xs {
			margin-bottom: 25px; }
		&--text-left {
			text-align: left;
			@include xs {
				text-align: center; }
			.justify-spec__part-option {
				padding-left: 16px;
				@include xs {
					padding-left: 0; } } } }
	&__part-option {
		background: $bg-blue;
		padding-top: 2px;
		padding-bottom: 2px;
		margin-bottom: 9px;
		@include m {
			margin-bottom: 5px; } }
	&__part-value {
		font-size: 20px;
		padding-right: 5px;
		@include rob;
		@include m {
			font-size: 14px; }
		@include xs {
			padding-right: 0; } } }
.select-line {
	@include clr;
	font-size: 0;
	line-height: 30px;
	margin-bottom: 29px;
	@include m {
		margin-bottom: 20px; }
	&__title {
		font-size: 18px;
		display: inline-block;
		vertical-align: top;
		margin-right: 12px;
		@include lg {
			font-size: 16px; }
		@include m {
			font-size: 14px;
			display: block;
			margin-right: 0; } }
	&__left, &__right {
		width: 50%;
		@include m {
			float: none;
			width: 100%; }

		.sssl {
			margin-right: 30px;
			&:last-of-type {
				margin-right: 0; }
			@include lg {
				margin-right: 15px; }
			@include m {
				margin-right: 10px; } } }

	&__left {
		float: left;
		@include m {
			margin-bottom: 20px; } }
	&__right {
		float: right;
		&--text-r {
			text-align: right; } } }
.radio-line {
	.radio {
		margin-right: 50px;
		&:last-of-type {
			margin-right: 0; }
		@include xs {
			display: block;
			margin-bottom: 15px;
			margin-right: 0;
			margin-left: 15px; } } }
