.referral-page {
	padding-top: 38px;
	border-bottom: 1px solid rgba($blue, 0.2);
	@include m {
		padding-top: 25px;
		padding-bottom: 25px; }
	&__title {
		margin-bottom: 33px;
		@include m {
			margin-bottom: 20px; } } }
.reftwo-col {
	display: flex;
	min-height: 430px;
	@include m {
		min-height: auto;
		flex-wrap: wrap; }
	&__img {
		flex-basis: 500px;
		position: relative;
		@include m {
			flex-basis: 100%;
			order: 1;
			text-align: center; }
		img {
			position: absolute;
			top: -56px;
			left: 0;
			@include m {
				position: static;
				max-width: 200px;
				height: auto; } } }
	&__descr {
		flex-basis: 670px;
		@include m {
			flex-basis: 100%;
			margin-bottom: 20px;
			order: 0; } } }
.text-block {
	border: 10px solid $blue;
	max-width: 670px;
	width: 100%;
	text-align: left;
	@include cover;
	padding: 10px 20px 21px;
	@include m {
		max-width: 100%;
		padding: 5px 10px 10px;
		border: 4px solid $blue; }
	p {
		margin-bottom: 28px;
		line-height: 1.6;
		&:last-of-type {
			margin-bottom: 0; } } }
