.select {
	position: relative;
	&_full {
		width: 100%; }
	select {
		-ms-appearance: none;
		appearance: none;
		cursor: pointer;
		user-select: none;
		width: 100%;
		background: #fff;
		height: 44px;
		line-height: 44px;
		padding: 0 30px 0 12px;
		border: 1px solid gray;
		border-radius: 4px;
		font-size: 16px;
		&::-ms-expand {
			display: none; }
		option {
			color: #000; } } }
.sssl {
	position: relative;
	display: inline-block;
	vertical-align: top;
	// min-width: 70px
	text-align: left;
	margin-bottom: 15px;
	@include ubt;
	select {
		visibility: hidden; }
	select {
		opacity: 0;
		position: absolute; }
	&.is-active {
		> ul {
			display: block; } }
	&.is-disabled {
		opacity: .2;
		> span {
			cursor: not-allowed;
			border-color: #000; } }
	&.error {
		> span, span.placeholder {
			border-color: red;
			color: transparent;
			&:after {
				opacity: 1; } } }
	> span {
		position: relative;
		display: block;
		cursor: pointer;
		padding: 0 23px 0 9px;
		height: 30px;
		line-height: 27px;
		white-space: nowrap;
		text-overflow: ellipsis;
		font-size: 18px;
		border: 1px solid $blue3;
		background: $bg-blue;
		&.placeholder {
			color: #9b9b9b;
			border-color: gray; }
		&:before {
			content: "";
			position: absolute; }
		&:before {
			background-image: url(data:image/svg+xml;base64,);
			width: 10px;
			height: 10px;
			opacity: 1;
			width: 12px;
			height: 10px;
			right: 6px;
			top: 9px;
			background-position: 50% 50%;
			background-repeat: no-repeat;
			transform-origin: 50% 50%;
			@include trans; } }
	> ul {
		position: absolute;
		display: none;
		left: 0;
		right: 0;
		top: 100%;
		background: $bg-blue;
		z-index: 100;
		border-left: 1px solid $blue;
		border-right: 1px solid $blue;
		border-bottom: 1px solid $blue;
		padding: 0;
		max-height: 200px;
		overflow-x: hidden;
		overflow-y: auto;

		list-style-type: none;

		li {
			padding: 10px 10px 7px;
			color: $black;
			cursor: pointer;
			line-height: 1;
			@include trans;
			&:hover {
				color: $blue; }
			&.disabled-item {
				text-decoration: line-through;
				&:hover {
					color: gray; } }
			&.placeholder-item {
				display: none; } } } }

.select-custom, .select-custom-big {
	&.is-active {
		ul {
			opacity: 1;
			visibility: visible; }
		> span {

			&:before {
				transform: rotateX(180deg);
				// right: 6px
				// width: 10px
				// height: 10px
 } } }				// background-image: url(data:image/svg+xml;base64,)
	ul {
		opacity: 0;
		display: block;
		visibility: hidden;
		transition: opacity .3s, visibility .3s;
		> li {
			font-size: 14px;
			transition: color .3s;

			&.is-active {
				display: none;

 } } }				// background: transparent
	> span {
		&:after {
			transition: transform .3s; } } }
.select-custom {
	min-width: 70px; }
.select-custom-big {
	min-width: 75px; }
