.hero {
	min-height: 550px;
	padding-top: 139px;
	@include cover;
	@include m {
		min-height: auto;
		padding: 30px 0; }
	&__title {
		margin-bottom: 50px; } }
.welcome {
	@include cover;
	padding-top: 22px;
	padding-bottom: 47px;
	@include m {
		padding-bottom: 25px; }
	&__inner {
		@include clr; }
	&__title {
		margin-bottom: 25px; }
	&__text {
		max-width: 730px;
		float: left;
		@include m {
			max-width: 100%; }
		p {
			margin-bottom: 25px;
			line-height: 1.5; } }
	&__img {
		float: right;
		width: calc(100% - 770px);
		@include m {
			display: none; }
		&-wrapper {
			max-width: 301px;
			display: inline-block;
			vertical-align: top; }
		img {
			height: 100%;
			width: 100%;
			object-fit: contain; } } }
.inv-plans {
	padding-top: 15px;
	padding-bottom: 30px;
	@include m {
		padding-bottom: 10px; }
	&--bg-mob {
		@include m {
			background: $bg-blue; } }
	&__title {
		margin-bottom: 54px;
		@include m {
			margin-bottom: 25px; } }
	&__inner {
		text-align: center;
		font-size: 0; } }
.plan-block {
	width: 230px;
	background: $white;
	text-align: center;
	display: inline-block;
	vertical-align: top;
	line-height: 1;
	padding-bottom: 17px;
	margin-right: 170px;
	@include lg {
		margin-right: 100px; }
	@include m {
		margin-right: 30px;
		margin-bottom: 20px; }
	@include xs {
		display: block;

		margin-left: auto;
		margin-right: auto; }
	&--last {
		margin-right: 0;
		@include lg {
			margin-right: 0; }
		@include xs {
			margin-right: auto; } }
	&__topline {
		font-size: 24px;
		@include rob;
		color: $white;
		padding: 12px 0 12px;
		margin-bottom: 10px; }
	&__perc {
		font-size: 72px;
		@include rob;
		color: $black;
		margin-bottom: 7px;
		font-weight: 700;
		@include m {
			font-size: 54px; } }
	&__after-perc {
		@include rob;
		color: $black;
		font-size: 32px;
		font-weight: 700;
		margin-bottom: 16px; }
	&__hourly {
		@include rob;
		font-size: 15px;
		opacity: 0.5;
		margin-bottom: 20px; }
	&__min, &__max {
		font-size: 18px;
		margin-bottom: 18px;
		strong {
			font-weight: 500; } }
	&__bottom-text {
		font-weight: 700;
		font-size: 18px; }
	&--l {
		.plan-block__topline {
			background: $blue1; } }
	&--m {
		.plan-block__topline {
			background: $blue2; } }
	&--d {
		.plan-block__topline {
			background: $blue; } } }

.calc-block {
	padding-top: 40px;
	@include m {
		padding-top: 20px; }
	&__title {
		margin-bottom: 23px; } }
.calc-wrapper {
	background-color: $bg-blue;
	border: 10px solid $blue;
	padding: 31px 60px 20px;
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
	@include lg {
		padding: 31px 30px 20px; }
	@include m {
		display: block;
		padding: 15px 15px 15px;
		border: 4px solid $blue; }
	.field {
		// margin-right: 65px
		// &:last-of-type
		// 	margin-right: 0
		@include m {
			margin-bottom: 15px; }

		input {
			text-align: center; } } }
.maximize-block {
	padding-top: 36px;
	padding-bottom: 57px;
	@include m {
		text-align: center;
		padding-top: 25px;
		padding-bottom: 25px; }
	&__img {
		margin-top: 12px;
		width: 100%;
		@include m {
			display: none; }
		img {
			@include lg {
				width: 500px;
				height: auto; } } }
	&__right {
		padding-left: 85px;
		@include m {
			padding-left: 0; } }
	&__title {
		margin-bottom: 15px;
		text-align: left;
		@include m {
			text-align: center; } }
	&__text {
		margin-bottom: 26px;
		p {
			margin-bottom: 8px; } }
	.two-col__left {
		@include m {
			display: none; } } }
.descr-blocks {
	background: $bg-blue;
	padding-top: 28px;
	padding-bottom: 23px;
	text-align: center;
	@include m {
		padding-bottom: 15px; }
	&__inner {
		display: flex;

		@include m {
			display: block; } } }
.descr-block {
	display: inline-block;
	vertical-align: top;
	width: 33.333%;
	flex-basis: 33.333%;
	flex-grow: 1;
	text-align: center;
	padding-left: 10px;
	padding-right: 10px;
	@include m {
		display: block;
		margin-left: auto;
		margin-right: auto;
		width: 100%;
		margin-bottom: 20px; }
	&__img {
		height: 170px;
		margin-bottom: 16px;
		@include vertical;
		@include m {
			height: 100px; }
		img {
			max-height: 170px;
			max-width: 250px;
			display: inline-block;
			vertical-align: middle;
			@include m {
				max-height: 100px; } } }
	&__title {
		font-size: 32px;
		font-weight: 700;
		margin-bottom: 7px;
		@include rob;
		@include m {
			font-size: 18px; } } }
