  color: #9b9b9b;
  border-color: gray;
}

.sssl > span:before {
  content: "";
  position: absolute;
}

.sssl > span:before {
  background-image: url(data:image/svg+xml;base64,);
  width: 10px;
  height: 10px;
  opacity: 1;
  width: 12px;
  height: 10px;
  right: 6px;
  top: 9px;
  background-position: 50% 50%;
  background-repeat: no-repeat;
  transform-origin: 50% 50%;
  transition: all 0.2s ease;
}

.sssl > ul {
  position: absolute;
  display: none;
  left: 0;
  right: 0;
  top: 100%;
  background: #cbdbd7;
  z-index: 100;
  border-left: 1px solid #193441;
  border-right: 1px solid #193441;
  border-bottom: 1px solid #193441;
  padding: 0;
  max-height: 200px;
  overflow-x: hidden;
  overflow-y: auto;
  list-style-type: none;
}

.sssl > ul li {
  padding: 10px 10px 7px;
