@mixin ubt {
	font-family: 'Ubuntu', sans-serif; }
@mixin rob {
	font-family: 'Roboto Slab', serif; }
@mixin lg {
	@media only screen and (max-width: "1199px") {
		@content; } }
@mixin l {
	@media only screen and (max-width: "1023px") {
		@content; } }
@mixin m {
	@media only screen and (max-width: "991px") {
		@content; } }
@mixin xs {
	@media only screen and (max-width: "767px") {
		@content; } }
@mixin r700 {
	@media only screen and (max-height: "700px") {
		@content; } }

$black: #151515;
$blue: #193441;
$blue-l: #3e606f;
$blue1: #91aab4;
$blue2: #3e606f;
$blue3: #193441;
$blue4: #d3ebe5;
$bg-blue: #cbdbd7;
$blue-soc: #3b5998;
$red: #ff0819;
$red-l: #fe3845;
$white: #fff;

body {
	background: $white;
	@include ubt;
	color: $black;
	line-height: 1.4;
	font-size: 18px;
	font-weight: 400;
	@include lg {
		font-size: 16px; }
	@include m {
		font-size: 14px; } }
button {
	appearance: none;
	border: none;
	background: none; }
h1,h2,h3,h4,h5,h6 {
	font-weight: normal; }
a {
	text-decoration: none;
	color: $black; }
strong {
	font-weight: 700; }
.out {
	display: flex;
	flex-direction: column;
	min-height: 100vh; }
.content-wrapper {
	flex: 1;
	@include cover; }
.container {
	padding: 28px 0 48px;
	@include cover;
	@include m {
		padding: 20px 0; }
	&--spec {
		padding: 40px 0 10px; }
	&--pt-36 {
		padding-top: 36px;
		padding-bottom: 25px;
		@include m {
			padding-top: 25px; } }
	&--pb-non {
		padding-bottom: 0; } }
.title-spec {
	span {
		color: $white;
		padding-left: 15px;
		padding-right: 5px;
		@include rob;
		font-size: 60px;
		font-weight: 700;
		line-height: 1.25;
		background: $blue;
		display: inline-block;
		vertical-align: top;
		@include m {
			font-size: 36px; } } }
.title {
	color: $black;
	@include rob;
	font-size: 32px;
	font-weight: 700;
	line-height: 1.1;
	margin-bottom: 28px;
	text-align: center;
	@include m {
		font-size: 20px; }
	&--white {
		color: $white; }
	&--mb {
		margin-bottom: 49px;
		@include m {
			margin-bottom: 20px; } }
	&--mb-md {
		margin-bottom: 36px;
		@include m {
			margin-bottom: 20px; } }
	&--md {
		font-size: 24px;
		margin-bottom: 63px;
		@include m {
			margin-bottom: 15px; } }
	&--mb-non {
		margin-bottom: 0; }
	&--left {
		text-align: left; } }
.btn {
	color: $white;
	font-size: 18px;
	line-height: 1;
	display: inline-block;
	vertical-align: top;
	text-align: center;
	@include ubt;
	@include trans;
	padding: 4px 8px 6px;
	font-weight: 400;
	&--blue {
		background: $blue;
		&:hover {
			background: $blue-l; } }
	&--red {
		background: $red;
		&:hover {
			background: $red-l; } }
	&--mb {
		margin-bottom: 29px; }
	&--i {
		padding: 12px 15px 14px;
		font-style: italic;
		min-width: 230px;
		font-size: 24px;
		@include m {
			padding: 8px 6px 10px;
			min-width: 180px; } }
	&--sm-it {
		font-style: italic;
		font-size: 18px;
		min-width: 130px;
		padding: 10px 8px 12px; } }
.soc-fb {
	background: $blue-soc;
	width: 20px;
	height: 20px;
	border-radius: 50%;
	text-align: center;
	line-height: 1;
	padding-top: 2px;
	display: inline-block;
	vertical-align: top;
	@include lg {
		padding-top: 3px; }
	svg {
		font-size: 14px;
		fill: $white; } }
.cripto-icon {
	background: $blue;
	width: 24px;
	height: 24px;
	border-radius: 50%;
	text-align: center;
	display: inline-block;
	vertical-align: top;
	position: relative;
	font-size: 0;
	line-height: 1;
	top: -2px;
	@include lg {
		width: 20px;
		height: 20px; }
	i {
		@include vcenter; }
	svg {
		fill: $white;
		font-size: 16px;
		@include lg {
			font-size: 14px; } }
	&--ltc {
		svg {
			font-size: 14px;
			@include lg {
				font-size: 12px; } } } }
.mob-menu-btn {
	display: none;
	padding-top: 4px;
	width: 20px;
	height: 14px;
	border-top: 2px solid $black;
	border-bottom: 2px solid $black;
	cursor: pointer;
	position: relative;
	&:before {
		content: '';
		position: absolute;
		top: -8px;
		bottom: -8px;
		right: -5px;
		left: -5px; }
	@include m {
		display: inline-block;
		vertical-align: top; }
	span {
		display: block;
		width: 100%;
		height: 2px;
		background: $black; } }
.two-col {
	@include clr;
	&__left, &__right {
		width: 50%;
		@include m {
			float: none;
			width: 100%; } }
	&__left {
		float: left;
		@include m {
			margin-bottom: 15px; } }
	&__right {
		float: right; } }
.is-hidden {
	overflow: hidden; }
.link {
	color: $blue;
	font-size: 18px;
	position: relative;
	@include trans;
	display: inline-block;
	vertical-align: top;
	@include lg {
		font-size: 16px; }
	@include m {
		font-size: 14px; }
	&:after {
		content: '';
		position: absolute;
		bottom: 2px;
		left: 0;
		width: 100%;
		height: 1px;
		background: $blue;
		opacity: 0.2;
		@include trans; }
	&:hover {
		color: $red;
		&:after {
			background: $red;
			opacity: 0.4; } } }
.full-texture-bg {
	@include cover;
	padding-top: 20px;
	padding-bottom: 48px;
	@include m {
		padding-bottom: 20px; } }
.article {
	font-size: 18px;
	@include ubt;
	line-height: 1.6;
	color: $black;
	@include lg {
		font-size: 16px; }
	@include m {
		font-size: 14px; }
	p {
		margin-bottom: 26px;
		@include m {
			margin-bottom: 12px; } }
	&--first-spec-text {
		p {
			&:first-of-type {
				text-align: center;
				@include rob;
				br {
					@include m {
						display: none; } } } } } }
.two-btn-line {
	text-align: center;
	margin-bottom: 25px;
	margin-top: 35px;
	@include m {
		margin-bottom: 0;
		margin-top: 30px; }
	.btn {
		&:first-of-type {
			margin-right: 170px;
			@include m {
				margin-right: 30px; }
			@include xs {
				margin-right: 10px; } }
		@include xs {
			margin-bottom: 20px;
			margin-left: 10px;
			margin-right: 10px; } } }
.mb {
	margin-bottom: 8px;
	&--sm {
		margin-bottom: 16px; }
	&--md {
		margin-bottom: 20px; }
	&--big {
		margin-bottom: 25px; } }
.centered {
	text-align: center; }
.centered-mob {
	@include m {
		text-align: center; } }
.big-text {
	font-size: 24px;
	font-weight: 700;
	@include m {
		font-size: 18px; } }
